.page-header {
  background: #1b2d37 url("../img/top-mask.png") center top repeat;
  overflow: hidden; }

.page-header__wrapper {
  padding-right: 256px;
  padding-left: 253px; }

.page-header__top {
  margin-bottom: 230px;
  padding-top: 33px;
  display: flex;
  justify-content: space-between; }

.page-header__logo {
  font-size: 0;
  display: block;
  margin: 0;
  padding: 0; }

@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .page-header {
    background-image: url("../img/top-mask-2x.png");
    background-size: 1920px auto; } }

@media (max-width: 1023px) {
  .page-header__wrapper {
    padding: 0 25px; }
  .page-header__top {
    margin-bottom: 116px;
    padding-top: 20px; } }

@media (max-width: 767px) {
  .page-header__wrapper {
    padding: 0 17px; }
  .page-header__top {
    margin-bottom: 232px;
    padding-top: 17px; }
  .page-header__logo-image {
    width: 38px;
    height: 38px; } }

.contacts {
  display: flex;
  align-items: center; }

.contacts__list {
  font-family: "PTMono", "Arial", sans-serif;
  font-weight: normal;
  font-size: 16px;
  line-height: 18px;
  color: #ffffff;
  list-style: none;
  display: flex;
  align-items: center;
  margin: 0 50px 0 0;
  padding: 0; }

.contacts__item:not(:last-child) {
  margin-right: 91px; }

.contacts__link {
  display: block;
  text-decoration: none;
  color: #ffffff; }
  .contacts__link svg {
    margin-right: 6px;
    vertical-align: middle; }

@media (max-width: 1023px) {
  .contacts__list {
    margin-right: 0;
    position: relative; }
    .contacts__list::before {
      position: absolute;
      top: 3px;
      left: -74px;
      width: 37px;
      height: 11px;
      border-top: 1px solid #ffffff;
      border-bottom: 1px solid #ffffff;
      content: ""; }
  .contacts__item:not(:last-child) {
    margin-right: 28px; }
  .contacts__link {
    font-size: 0;
    line-height: 0; }
    .contacts__link--email svg {
      width: 25.33px;
      height: 18.67px; }
    .contacts__link--phone svg {
      margin-right: 0;
      width: 24px;
      height: 24px; }
  .contacts__btn {
    display: none; } }

@media (max-width: 767px) {
  .contacts__list::before {
    left: -65px; }
  .contacts__item:not(:last-child) {
    margin-right: 16px; } }

.button {
  font-family: "RobotoCondensed", "Arial", sans-serif;
  font-weight: bold;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #ffffff;
  text-decoration: none;
  background-color: transparent;
  border: 1px solid #ffffff;
  border-radius: 5px;
  cursor: pointer;
  padding: 14px 26px; }
  .button--big {
    display: inline-block;
    padding: 18px 46px;
    font-family: "RobotoCondensed", "Arial", sans-serif;
    font-weight: bold;
    font-size: 16px;
    line-height: 19px;
    background-color: #00b2ff;
    border: none; }

@media (max-width: 1023px) {
  .button--big {
    padding: 18px 31px; } }

.promo {
  position: relative;
  z-index: 1;
  margin-left: -4px;
  margin-bottom: 273px; }
  .promo::after {
    position: absolute;
    top: -128px;
    right: -37px;
    width: 671px;
    height: 573px;
    background-image: url("../img/boards-decoration-big.svg");
    background-repeat: no-repeat;
    content: ""; }

.promo__title {
  margin-bottom: 28px;
  font-family: "RobotoCondensed", "Arial", sans-serif;
  font-weight: bold;
  font-size: 80px;
  line-height: 94px;
  letter-spacing: 0.07em;
  text-transform: uppercase;
  color: #ffffff; }

.promo__text {
  margin: 0 0 44px 0;
  padding-left: 97px;
  max-width: 690px;
  position: relative;
  font-family: "PTMono", "Arial", sans-serif;
