  font-size: 22px;
  line-height: 34px;
  color: #ffffff; }
  .promo__text::before {
    position: absolute;
    top: 16px;
    left: 4px;
    width: 66px;
    height: 2px;
    content: "";
    background-color: #ffffff; }

.promo__link {
  margin-left: 97px; }

.promo__image {
